.cards {
    padding: 2rem;
    background: #fff;
    background-color: black;
     
    
  }
  
  h1 {
    text-align: center;
    color: white;
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
    
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items {
    margin-bottom: 24px;
    
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    
    
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(67, 67, 67, 0.5);
    -webkit-filter: drop-shadow(0 6px 20px rgba(197, 203, 216, 0.017));
    filter: drop-shadow(0 6px 20px rgba(197, 203, 216, 0.017));
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
    
    
  }

 
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 15px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #6d76f7;
    box-sizing: border-box;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: white;
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 5px;
  }

  .cards__buttons{

   
  bottom: 0;
   
     
  }

  .cards__buttons button{

    position: absolute;
    max-width: 200px;
    font-size: 12px;
    font-weight: 700;
    color:white;
    background-color: transparent;
    border: none;
    text-decoration: none;
    outline: none;
    bottom: 0;
    cursor: pointer;
    }

    .cards__buttons button:hover{

        background: #6d76f7;
        color: white;
        transition: 0.3s ease-out ;
        }

  .demo__button{
    display: inline-block;
    right: 0;
    height: auto;
    padding: 6px 8px;
    transform: scale(1.3);
  
  }

  .demo__button:hover{
    transform: scale(1.2);
  }

  .source__button{
    display: inline-block;
    left: 0;
    height: auto;
    padding: 6px 8px;
    transform: scale(1.3);
  }

  .source__button:hover{
    transform: scale(1.2);
  }

  

  
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
    
  }